import React from 'react';


class Result extends React.Component {

  render() {
    return (
      <div id="score">
        <p>
          Current score: {this.props.score} out of {this.props.count} 
          {this.props.count > 0 ? ' (' + (this.props.score / this.props.count * 100).toFixed(1) + ' %)' : ''}
          <br />
          <small>Max score: {this.props.totalScore}</small>  
        </p>
      </div>
    );
  }

}

export default Result;
