import React from 'react';
import spells from './data/spells.json'
import './css/skeleton.css'
import './App.css';
import Question from './Question';
import AnswerHard from './AnswerHard';
import AnswerEasy from './AnswerEasy';
import Result from './Result';


class App extends React.Component {

  constructor(props) {
    super(props);
    this.spells = spells.spells;
    this.totalScore = this.spells.length;
    this.state = {
      remainingSpells: this.spells,
      spell: "",
      multipleChoices: [{"type": "", "incantation": "", "effect": "" }, {"type": "", "incantation": "", "effect": "" }, {"type": "", "incantation": "", "effect": "" }, {"type": "", "incantation": "", "effect": "" }],
      guess: "",
      score: 0,
      count: 0,
      hardMode: false
    };
  }

  render() {
    let difficultyMode;
    if (this.state.hardMode) {
      difficultyMode = (
        <AnswerHard 
          getAnswer={this.getAnswer} 
          clickedPass={this.passQuestion} 
          input={this.state.guess} 
        />
      );
    } else {
      difficultyMode = (
        <AnswerEasy 
          getAnswer={this.getAnswer} 
          multipleChoices={this.state.multipleChoices} 
          correctChoice={this.state.spell.incantation}
        />
      );
    }

    return (
      <div className="App">

        <div className="difficultySwitch">
          <button onClick={this.switchDifficulty}>
            {this.state.hardMode ? 'Easy Mode' : 'Hard Mode'}
          </button>
        </div>

        <div className="header">
          <img className="App-logo" alt="logo" src={process.env.PUBLIC_URL + '/hogwarts-logo.png'} /> 

          <div>
            <h1>
              {this.state.hardMode ? "Spell the Spell" : "Guess the Spell"}
            </h1>
          </div>
        </div>

        <div className="content">

          <Question 
            spell={this.state.spell} 
          />

          {difficultyMode}

          <Result 
            count={this.state.count}
            score={this.state.score}
            totalScore={this.totalScore}
          />

        </div>
        
      </div>
    );
  }

  componentDidMount() {
    this.getRandomQuestion();
  }

  getRandomQuestion = () => {
    // Select a random item from array
    let randomIndex = Math.floor(Math.random() * this.state.remainingSpells.length);
    let spell = this.state.remainingSpells[randomIndex];
    console.log("Answer is: " + spell.incantation);
    
    // Remove selected item from array
    let newRemainingSpells = [...this.state.remainingSpells]
    newRemainingSpells.splice(randomIndex, 1);

    // Get multiple choices
    let choices = this.getMultipleChoices(spell);

    this.setState({
      spell: spell,
      guess: "",
      remainingSpells: newRemainingSpells,
      multipleChoices: choices
    });
  }

  getMultipleChoices = (correctChoice) => {
    // Get array of option and remove the correct one
    let allChoices = [...this.spells]
    let correctChoiceIndex = allChoices.indexOf(correctChoice);
    if (correctChoiceIndex !== -1) allChoices.splice(correctChoiceIndex, 1);

    // Select 3 "wrong" options at random (shuffle the array and pick first 3)
    let shuffled = allChoices.sort(() => 0.5 - Math.random());
    let selected = shuffled.slice(0, 3);

    // Merge with the correct answer and shuffle
    let multipleChoices = [correctChoice].concat(selected);
    let shuffledMultipleChoices = multipleChoices.sort(() => 0.5 - Math.random());
    // console.log(shuffledMultipleChoices);
    return shuffledMultipleChoices;
  }

  getAnswer = (answer) => {
    this.setState({
      guess: answer
    }, this.checkGuess);
  }

  passQuestion = () => {
    this.setState(prevState => {
      return {
        count: prevState.count + 1
      }
    }, this.getRandomQuestion);
  }

  checkGuess = () => {
    if (this.state.guess.toLowerCase() === this.state.spell.incantation.toLowerCase()) {
      // Correct answer
      this.setState(prevState => {
        return {
          score: prevState.score + 1,
          count: prevState.count + 1
        }
      }, this.getRandomQuestion);
    } else {
      // Wrong answer
      if (this.state.hardMode === false) {
        this.setState(prevState => {
          return {
            count: prevState.count + 1
          }
        }, this.getRandomQuestion);
      }
    }
  }

  switchDifficulty = () => {
    this.setState(prevState => {
      return {
        hardMode: !prevState.hardMode
      }
    })
  }

}

export default App;
