import React from 'react';


class AnswerEasy extends React.Component {

  render() {
    return (
      <div className="answerEasy">

        <div className="row">
          <div className="three offset-by-three columns">
            <button className="multipleChoiceButton" onClick={this.selectChoice}>
              {this.props.multipleChoices[0].incantation}
            </button>
            <button className="multipleChoiceButton" onClick={this.selectChoice}>
              {this.props.multipleChoices[1].incantation}
            </button>
          </div>
            
          <div className="three columns">
            <button className="multipleChoiceButton" onClick={this.selectChoice}>
              {this.props.multipleChoices[2].incantation}
            </button>
            <button className="multipleChoiceButton" onClick={this.selectChoice}>
              {this.props.multipleChoices[3].incantation}
            </button>
          </div>
        </div>

        <br />

      </div>
    );
  }

  selectChoice = (event) => {
    event.persist()
    if (event.target.innerHTML.toLowerCase() === this.props.correctChoice.toLowerCase()) {
      event.target.classList.add('correctButton');
    } else {
      event.target.classList.add('wrongButton');
    }
    
    setTimeout(
      () => {
        this.props.getAnswer(event.target.innerHTML);
        event.target.classList.remove('correctButton');
        event.target.classList.remove('wrongButton');
      }, 150
    )
  }

}

export default AnswerEasy;
