class AnswerHard extends React.Component {

  // constructor(props) {
  //   super(props);
  //   this.state = {
  //     guess: "",
  //   };
  // }

  render() {
    return (
      <div className="answerHard">
        <div>
          <input type="text" value={this.props.input} onChange={this.changeInput} ref={(ip) => this.answerInput = ip} />
        </div>

        <button onClick={this.passButton}>Pass</button>
      </div>
    );
  }

  // changeInput = (event) => {
  //   this.setState({
  //     guess: event.target.value
  //   }, this.updateGuess);
  // }

  // updateGuess = () => {
  //   this.props.getAnswer(this.state.guess);
  // }

  changeInput = (event) => {
    this.props.getAnswer(event.target.value);
  }

  passButton = (event) => {
    this.props.clickedPass();
    this.answerInput.focus()
  }

}

export default AnswerHard;
