import React from 'react';
// import spells from './data/spells.json'
// import './css/skeleton.css'

class Question extends React.Component {

  render() {
    return (
      <div id="question">
        <p>
          <code>{this.props.spell.type}</code>
          <br />
          {this.props.spell.effect}
        </p>
      </div>
    );
  }

}

export default Question;
